.home-page {
    display: flex;
    height: 100vh;
  }
  
  .sidebar {
    width: 300px;
    background-color: #f5f5f5;
    border-right: 1px solid #ddd;
    display: flex;
    flex-direction: column;
  }
  
  .profile {
    display: flex;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid #ddd;
  }
  
  .profile-pic {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 15px;
  }
  
  .username {
    font-size: 1.2rem;
  }
  
  .chat-list {
    flex: 1;
    overflow-y: auto;
  }
  
  .chat-item {
    display: flex;
    align-items: center;
    padding: 15px;
    border-bottom: 1px solid #ddd;
    cursor: pointer;
  }
  
  .chat-item:hover {
    background-color: #e0e0e0;
  }
  
  .chat-avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 15px;
  }
  
  .chat-info {
    flex: 1;
  }
  
  .chat-name {
    margin: 0;
    font-size: 1rem;
  }
  
  .chat-preview {
    margin: 0;
    color: #777;
  }
  
  .main-chat {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .chat-header {
    background-color: #075e54;
    color: white;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .new-chat-btn {
    background-color: #128c7e;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 15px;
    background-color: #e5ddd5;
  }
  
  .message {
    margin-bottom: 15px;
  }
  
  .message-text {
    background-color: #dcf8c6;
    padding: 10px;
    border-radius: 10px;
    max-width: 60%;
    display: inline-block;
  }
  
  .message-time {
    font-size: 0.75rem;
    color: #888;
    display: block;
    margin-top: 5px;
  }
  
  .message-input-container {
    padding: 15px;
    background-color: white;
    border-top: 1px solid #ddd;
    display: flex;
    align-items: center;
  }
  
  .message-input {
    flex: 1;
    padding: 10px;
    border: none;
    border-radius: 20px;
    background-color: #f0f0f0;
    font-size: 1rem;
  }
  
  .send-btn {
    background-color: #075e54;
    color: white;
    border: none;
    padding: 10px 15px;
    margin-left: 10px;
    border-radius: 50%;
    cursor: pointer;
  }
  